export default function StudentProfilePage({ params }: { params: { studentId: string } }) {
  const student = getStudentData(params.studentId);

  if (!student) {
    return <div>Student not found.</div>; // Handle case where student data isn't available
  }

  return (
    <div className="container mx-auto py-8 px-4 md:px-6">
      <div className="grid gap-8 lg:grid-cols-3">

        {/* Left Column: Profile Info & Stats */}
        <div className="lg:col-span-1 space-y-6">
          {/* Profile Card */}
          <Card>
            <CardHeader className="text-center">
              <Avatar className="w-24 h-24 mx-auto mb-4 border-2 border-primary">
                <AvatarImage src={student.avatar} alt={student.name} />
                <AvatarFallback>{student.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
              </Avatar>
              <CardTitle className="text-2xl">{student.name}</CardTitle>
              <CardDescription>{student.email}</CardDescription>
              <CardDescription className="text-xs">Joined: {format(student.joinedDate, 'PPP')}</CardDescription>
            </CardHeader>
            <CardContent className="text-sm text-center">
              <p className="text-muted-foreground">{/* Placeholder for a short bio or status */}</p>
            </CardContent>
            <CardFooter className="justify-center">
               {/* Add relevant actions if needed, e.g., Message Student (future feature) */}
              <Button variant="outline" size="sm">
                <MessageSquare className="mr-2 h-4 w-4" /> Message (Soon)
              </Button>
            </CardFooter>
          </Card>

          {/* Stats Card */}
          <Card>
            <CardHeader>
              <CardTitle>Statistics</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3 text-sm">
              <div className="flex justify-between">
                <span className="text-muted-foreground">Total Sessions:</span>
                <span className="font-medium">{student.totalSessions}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Average Score:</span>
                <span className="font-medium">{student.averageScore}%</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Last Session:</span>
                <span className="font-medium">{format(student.lastSession, 'PP')}</span>
              </div>
              {student.upcomingSession && (
                <div className="flex justify-between text-primary">
                  <span className="text-muted-foreground">Next Session:</span>
                  <span className="font-medium">{format(student.upcomingSession, 'PPp')}</span>
                </div>
              )}
            </CardContent>
          </Card>

           {/* Subjects Card */}
            <Card>
              <CardHeader>
                  <CardTitle className="inline-flex items-center gap-2">
                     <BookOpen className="h-5 w-5"/> Subjects
                  </CardTitle>
              </CardHeader>
              <CardContent className="flex flex-wrap gap-2">
                  {student.subjects.map((subject) => (
                     <Badge key={subject} variant="secondary">{subject}</Badge>
                  ))}
              </CardContent>
            </Card>

        </div>

        {/* Right Column: Goals & Activity */}
        <div className="lg:col-span-2 space-y-6">
          {/* Goals Card */}
          <Card>
            <CardHeader>
              <CardTitle className="inline-flex items-center gap-2">
                 <Target className="h-5 w-5"/> Learning Goals
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-muted-foreground">{student.goals || "No goals set yet."}</p>
            </CardContent>
          </Card>

          {/* Recent Activity Card */}
          <Card>
            <CardHeader>
              <CardTitle>Recent Activity</CardTitle>
            </CardHeader>
            <CardContent>
              <ul className="space-y-4">
                {student.recentActivity.map((activity, index) => (
                  <li key={index} className="flex items-start gap-3">
                    <div className="mt-1">
                      {activity.type === 'session' && <Calendar className="h-4 w-4 text-primary" />}
                      {activity.type === 'exercise' && <BookOpenText className="h-4 w-4 text-accent" />}
                      {activity.type === 'document' && <FileText className="h-4 w-4 text-secondary-foreground/50" />}
                    </div>
                    <div className="flex-1">
                      <p className="text-sm font-medium">{activity.description}</p>
                      <p className="text-xs text-muted-foreground">{format(activity.date, 'PPP')}</p>
                    </div>
                  </li>
                ))}
              </ul>
            </CardContent>
          </Card>
        </div>

      </div>
    </div>
  );
} 